"use client";
import ConfirmationModal from '@/components/ConfirmationModal/ConfirmationModal';
import CustomTable from '@/components/CustomTable/CustomTable';
import { TableTopContent } from '@/components/CustomTable/TableTopContent';
import FormModal from '@/components/FormModal/FormModal';
import UseTableControls from '@/hooks/useTableControls';
import { GETService } from '@/service/CommanService';
import { Button } from '@heroui/react';
import React, {  useEffect, useState } from 'react'

function page() {
  const [method, setMethod] = useState("Create");
  const [jobs, setJobs] = useState([]);
  const [totalRecords, setTotalRecords] = useState(0);
  const {
    page,
    pages,
    setPage,
    rowsPerPage,
    onRowsPerPageChange,
    totalItems,
    onClear,
    isOpen,
    setIsOpen,
    onRowSelected,
    selectedRowItem,
    isConfirmModalOpen,
    setIsConfirmModalOpen,
    deleteController,
    recordToDelete,
    setRecordToDelete,
    filterValue,
    onSearchChange,
  } = UseTableControls(jobs, totalRecords);
  useEffect(() => {
    const fetchJobs = async () => {
      try {
        const response = await GETService({ endpoint: 'api/v1/jobs/get-jobs' });
        setJobs(response.data);
        setTotalRecords(response.totalRecords);
      } catch (error) {
        console.error('Error fetching jobs:', error);
      }
    };
    fetchJobs();
  }, []);
 const rows = jobs.map((job) => ({
    id: job.id,
    taskName: job.taskName,
    priority: job.priority,
    status: job.status,
    payload: job.payload,
    createdAt: job.createdAt,
    updatedAt: job.updatedAt,

  }));
  const HeaderColumns = [
    { name: "Task Name", uid: "taskName" },
    { name: "Priority", uid: "priority" },
    { name: "Status", uid: "status" },
    // { name: "Payload", uid: "payload" },
    { name: "Created At", uid: "createdAt" },
    { name: "Updated At", uid: "updatedAt" },
    { name: "Actions", uid: "actions" },
  ]
  return (
    <div className="flex flex-col gap-4 p-5">
      <p className="text-lg font-semibold">Job Scheduler</p>
      {/* <div className="flex flex-row justify-between items-center gap-5"> */}
      <div className="flex flex-col sm:flex-row flex-wrap gap-3 sm:gap-5 items-stretch">
        <div className="flex min-w-[200px]">
          <TableTopContent
            filterValue={filterValue}
            onClear={onClear}
            onRowsPerPageChange={onRowsPerPageChange}
            rowsPerPage={rowsPerPage}
            totalItems={totalItems}
            onSearchChange={onSearchChange}
          />
        </div>
        <Button
          onPress={() => {
            setIsOpen(true), setMethod("Create");
          }}
          className="w-full sm:w-auto text-white"
          color="primary"
        >
          Add Job
        </Button>
      </div>
      {/* </div> */}
      {jobs && (
        <CustomTable
          page={page}
          pages={pages}
          setPage={setPage}
          rowsPerPage={rowsPerPage}
          onRowsPerPageChange={onRowsPerPageChange}
          totalItems={totalItems}
          headerColumns={HeaderColumns}
          items={rows}
          setMethod={setMethod}
          setIsOpen={setIsOpen}
          onRowSelected={onRowSelected}
        />
      )}
      {isOpen && (
        <FormModal
          setMethod={setMethod}
          isOpen={isOpen}
          setIsOpen={setIsOpen}
          onRowSelected={onRowSelected}
        >
          
        </FormModal>
      )}
      {/* <ConfirmationModal
        isConfirmModalOpen={isConfirmModalOpen}
        setIsConfirmModalOpen={setIsConfirmModalOpen}
        setMethod={setMethod}
        onDeleteConfirm={onDeleteConfirm}
        onRowSelected={onRowSelected}
      /> */}
    </div>
  )
}

export default page